<style scoped>
.lazy-wrap {
    display: block;
    box-sizing: border-box;
    width: 83.3vmin;
    height: 100vmin;
    margin: 0 auto;
    border: 1px solid #e1e1e1;
    border-radius: 4px;
    overflow: hidden;
}

.img-wrap {
    display: block;
    box-sizing: border-box;
    width: 83.3vmin;
    height: 83.3vmin;
    margin: 0 auto;
}

.img-wrap img {
    width: 100%;
}

.lazy-content .desc {
    color: #333;
    font-size: 14px;
    box-sizing: border-box;
    height: 16.7vmin;
    line-height: 1.5;
    padding: 10px 5px;
}

.desc p {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
}
</style>
<template>
    <div class="lazy-wrap">
        <div class="lazy-content" v-show="state!=='hide'">
            <div class="img-wrap">
                <img v-bind:src="imgSrc">
            </div>
            <div class="desc" v-if="desc">
                <p>{{desc}}</p>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data: function() {
        return {
            desc: "This is a lazy item.This is a lazy item.This is a lazy item.This is a lazy item.This is a lazy item.This is a lazy item.",
            smallSrc: "../img/small.jpg",
            bigSrc: "../img/big.jpg",
            state: "big"
        };
    },
    computed: {
        imgSrc: function() {
            return this.state === "big" ? this.bigSrc : this.smallSrc;
        }
    }
}
</script>



